.wrapper {
    
}

.logo {
    padding-left: 3%;
    width: 150px;
}

.header_block {
    display: flex;
    align-items: flex-start;
}

.header_block :first-child {
    align-self: center;
}

.buttons_block {
    padding-right: 3%;
    margin-left: auto;
    align-self: center;
}

.add_btn {
    font-size: 18px;
    transition-duration: 0.3s;
    border: none;
    background-color: rgb(0, 129, 39);
    color: white;
    padding: 10px;
    border-radius: 3px;
    margin-left: 70%;
    bottom: 10;
    box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}

.btn {
    font-size: 18px;
    margin-left: 3px;
    transition-duration: 0.3s;
    border: none;
    background: none;
    color: white;
    padding: 10px;
    border-radius: 3px;
    background-color: rgba(22, 21, 25, 0.2);
}

.add_btn:hover{
    cursor: pointer;
    transition-duration: 0.3s;
    background: rgb(0, 71, 21);
}
.btn:hover{
    cursor: pointer;
    transition-duration: 0.3s;
    background: rgb(255, 28, 11);
}


.modalTitle {
    color: white;
}

.titleField {
    margin-bottom: 2%;
}

.typeField {
    margin-left: 2%;
    margin-bottom: 3%;
}

.genreField {
    margin-top: 3%;
    margin-bottom: 3%;
}
.ratingField {
   margin-right: 10px;
   margin-bottom: 10px;
}
.modalButtons {
    display: flex;
    justify-content: center;
    margin-top: 3%;
}

.modalBtn {
    margin-left: 10px;
}



